import BaseContent from '../../components/BaseContent/BaseContent'
import LottieWebCimo from '../../components/LottieWebCimo/lottie-web-cimo'

export default {
  name: 'my-lottie',
  components: {
    LottieWebCimo,
    BaseContent
  },
  data () {
    return {
      defaultOptions: {
        path: 'https://assets1.lottiefiles.com/packages/lf20_gzl797gs.json',
        loop: true,
        animationSpeed: 1
      },
      anim: {},
      content: '',
      test: {},
      isLottieF: false
    }
  },

  // 第四步：定义方法
  methods: {

    play () {
      this.$refs.lottie_web.play()
    },

    stop () {
      this.$refs.lottie_web.stop()
    },

    pause () {
      this.$refs.lottie_web.pause()
    },

    handleLottieFinish (e) {
      this.isLottieF = e
    },

    getMsg () {
      const query = {
        url: './data/lottieData.md',
        method: 'get',
        responseType: 'text'
      }
      this.$fetchData(query).then(res => {
        this.content = res.data
      }).catch(error => {
        console.log(error)
      })
    }
  },

  mounted () {
    this.getMsg()
  }
}
